.controls {
  display: -ms-flexbox;
  display: flex;
  padding: 18px;
}
.controls span {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-right: 6px;
}
.controls span:nth-of-type(1) {
  background: #ff5f56;
}
.controls span:nth-of-type(2) {
  background: #ffbd2e;
}
.controls span:nth-of-type(3) {
  background: #27c93f;
}



